<template>
    <a class="activities-item-action" @click="actionFunction">
        {{ text }}
    </a>
</template>

<script>

import { openFile, openTerminal, showFileDiff } from '../utils';

export default {
    name: 'activities-item-action',
    props: ["activity", "localize"],
    data: function() {
        return {
            text: '',
            actionFunction: null
        };
    },
    created() {
        switch (this.activity.data.type) {
            case 'fileOpen':
                this.text = this.localize('Open file');
                this.actionFunction = () => openFile(this.activity.userId, this.activity.data.fileName);
                break;
            case 'textSelection':
                this.text = this.localize('See selection(s)');
                this.actionFunction = () => openFile(this.activity.userId, this.activity.data.fileName, this.activity.data.range);
                break;
            case 'fileEdit':
                this.text = this.localize('See edit(s)');
                this.actionFunction = () => showFileDiff(this.activity.data.fileName, this.activity.timestamp);
                break;
            case 'terminal':
                this.text = this.localize('Open terminal');
                this.actionFunction = () => openTerminal(this.activity.data.terminal);
                break;
            default:
                this.text = '';
                this.actionFunction = () => {};
                break;
        }
    }
}

</script>

<style>

.activities-item-action:hover {
    cursor: pointer;
    text-decoration: underline;
}

</style>