/*global acquireVsCodeApi*/
export const vscode = acquireVsCodeApi();

export function sendMessage(type, body) {
    vscode.postMessage({
        type,
        body
    });
}

export function formattedTime(ts) {
    const d = new Date(+ts * 1000);
    return d.toLocaleTimeString([], { hour: "2-digit", minute: "2-digit" });
}

export function openFile(userId, fileName, selection = null) {
    return sendMessage("open", { userId, fileName, selection });
}

export function openTerminal(terminalInfo) {
    return sendMessage("open_terminal", terminalInfo);
}

export function showFileDiff(fileName, timestamp) {
    return sendMessage("diff", { fileName, timestamp });
}