<template>
    <div class="activities-section">
        <div v-for="activity in filteredActivities" :key="activity.timestamp" tabindex="0">
            <activity-item :activity="activity" :users="users" :localize="localize">
            </activity-item>
        </div>
    </div>
</template>

<script>
import ActivityItem from './ActivityItem.vue';
import { sendMessage } from '../utils';

export default {
    name: 'activities-section',
    props: ['activities', 'users', 'searchText', 'userFilter', 'localize'],
    components: {
        ActivityItem,
    },
    data: function () {
        return {
            activitiesCurrentLength: 0,
        };
    },
    computed: {
        filteredActivities() {
            let filteredActivities = this.activities;

            if (this.searchText.trim() === '') {
                filteredActivities = this.activities.filter((activity) =>
                    this.userFilterIncludesActivity(activity)
                );
            } else {
                filteredActivities = this.activities.filter(
                    (activity) =>
                        this.userFilterIncludesActivity(activity) &&
                        (this.checkActivityByType(activity) || this.checkActivityByFile(activity))
                );
            }

            return filteredActivities;
        },
    },
    updated() {
        const newLength = this.activities.length;
        if (newLength !== this.activitiesCurrentLength) {
            this.activitiesCurrentLength = newLength;
            this.$el.scrollTop = this.$el.scrollHeight;
        }
    },
    methods: {
        checkActivityByType(activity) {
            return activity.data.type.toLowerCase().includes(this.searchText.toLowerCase().trim());
        },
        checkActivityByFile(activity) {
            if (!Object.prototype.hasOwnProperty.call(activity.data, 'fileName')) return false;

            const fileName = activity.data.fileName.toLowerCase().split('/').pop();

            return fileName.startsWith(this.searchText.toLowerCase().trim());
        },
        userFilterIncludesActivity(activity) {
            const allUsersString = this.localize('All users');
            if (this.userFilter === allUsersString) return true;

            return this.users[activity.userId] === this.userFilter;
        },
    },
    mounted() {
        sendMessage('is_ready');
    },
};
</script>

<style scoped>
.activities-section {
    overflow: auto;
}
</style>
