<template>
    <div id="app" :style="cssProps">
        <div class="vue-container">
            <activities-section
                :activities="activities"
                :users="users"
                :localize="localize"
                :searchText="searchText"
                :userFilter="userFilter">
            </activities-section>
            <activities-search
                :users="users"
                :localize="localize"
                :searchText="searchText"
                :userFilter="userFilter"
                v-on:searchTextUpdated="onSearchTextUpdated"
                v-on:userFilterUpdated="onUserFilterUpdated">
            </activities-search>
        </div>
    </div>
</template>

<script>
import Vue from 'vue';
import {
    provideVSCodeDesignSystem,
    vsCodeDropdown,
    vsCodeOption,
    vsCodeTextField,
} from '@vscode/webview-ui-toolkit';
import ActivitiesSection from './components/ActivitiesSection.vue';
import ActivitiesSearch from './components/ActivitiesSearch.vue';

provideVSCodeDesignSystem().register(vsCodeDropdown(), vsCodeOption(), vsCodeTextField());

export default {
    name: 'app',
    props: ['data'],
    components: {
        ActivitiesSection,
        ActivitiesSearch,
    },
    data() {
        return {
            userFilter: '',
            searchText: '',
        };
    },
    computed: {
        activities() {
            return this.data ? this.data.activities : [];
        },
        users() {
            return this.data ? this.data.users : {};
        },
        localizedStrings() {
            return this.data ? this.data.localizedStrings : {};
        },
        cssProps() {
            return {
                '--chat-code-font-family': this.data ? this.data.css.fontFamily : `monospace`,
                '--chat-font-size': this.data ? `${this.data.css.fontSize}px` : `12px`,
            };
        },
    },
    methods: {
        onSearchTextUpdated(event) {
            this.searchText = event.target.value;
        },
        onUserFilterUpdated(event) {
            this.userFilter = event.target.value;
        },
        localize(key) {
            return this.localizedStrings[key] || key;
        },
    },
    mounted() {
        this.userFilter = this.localize('All users');
    },
};
</script>

<style>
html,
body,
#app {
    height: 100%;
    font-size: var(--chat-font-size);
}

code {
    font-family: var(--chat-code-font-family);
}

.vue-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    height: 100%;
    overflow: hidden;
}

.header-section {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;
    position: sticky;
}

.messages-section {
    overflow: auto;
}

.date-heading {
    color: var(--vscode-sideBar-foreground);
    font-weight: normal;
}

.timestamp {
    color: var(--vscode-sideBar-foreground);
}

.username {
    cursor: pointer;
}
</style>
