<template>
    <div class="activities-item">
        <div class="activities-item-group">
            <activity-item-message
                :data="activity.data"
                :user="user"
                :users="users"
                :localize="localize">
            </activity-item-message>
            &nbsp;
            <span class="timestamp">{{ readableTimestamp }}</span>
        </div>
        <activity-item-action :activity="activity" :localize="localize"></activity-item-action>
        <div class="horizontal-line"></div>
    </div>
</template>

<script>
import { formattedTime } from '../utils';
import ActivityItemMessage from './ActivityItemMessage.vue';
import ActivityItemAction from './ActivityItemAction.vue';

export default {
    name: 'activity-item',
    props: ['activity', 'users', 'localize'],
    components: {
        ActivityItemMessage,
        ActivityItemAction,
    },
    computed: {
        readableTimestamp() {
            return formattedTime(this.activity.timestamp);
        },
        user() {
            return this.users[this.activity.userId.toString()] || null;
        },
    },
};
</script>

<style>
.activities-item {
    margin-bottom: 10px;
}

.horizontal-line {
    width: 100%;
    height: 0.5px;
    margin-top: 10px;
    background-color: var(--vscode-sideBar-foreground);
}

.activities-item-group {
    width: 100%;
}
</style>
