<template>
    <span class="activities-item-message" v-html="toDisplayMessage"> </span>
</template>

<script>
export default {
    name: 'activity-item-message',
    props: ['data', 'user', 'users', 'localize'],
    computed: {
        toDisplayMessage() {
            let message;
            switch (this.data.type) {
                case 'fileOpen':
                    message = this.localize('activities.fileOpen')
                        .replace('$(user)', `<b>${this.user}</b>`)
                        .replace('$(fileName)', `<i>${this.data.fileName}</i>`);
                    break;
                case 'textSelection': {
                    const start = this.data.range[0];
                    const end = this.data.range[1];
                    const selection = `${start.line + 1}:${start.character + 1}-${end.line + 1}:${end.character + 1}`;
                    const selectionString = `${this.data.fileName}:${selection}`;
                    message = this.localize('activities.textSelection')
                        .replace('$(user)', `<b>${this.user}</b>`)
                        .replace('$(selection)', `<i>${selectionString}</i>`);
                    break;
                }
                case 'fileEdit': {
                    let userNames = this.user;
                    const coEditorIds = this.data.coEditorIds;
                    if (coEditorIds.length > 0) {
                        userNames += this.formatCoEditorNames(coEditorIds);
                    }
                    message = this.localize('activities.fileEdit')
                        .replace('$(user)', `<b>${userNames}</b>`)
                        .replace('$(fileName)', `<i>${this.data.fileName}</i>`);
                    break;
                }
                case 'terminal':
                    message = this.getTerminalDisplayMessage(this.data);
                    break;
                default:
                    message = ``;
                    break;
            }
            return message;
        },
    },
    methods: {
        formatCoEditorNames(coEditorIds) {
            const userNames = coEditorIds.map(coEditorId => this.users[coEditorId]);
            const lastUser = userNames.pop();
            const userNamesString = userNames.join(", ");
            if (userNamesString === "") {
                return ` ${this.localize('and')} ${lastUser}`;
            }
            return `, ${userNamesString} ${this.localize('and')} ${lastUser}`;
        },
        getTerminalDisplayMessage(terminalData) {
            const user = this.user;
            const { terminal, action } = terminalData;
            let terminalMessage;
            let actionMessage;
            switch (action) {
                case 'share':
                    terminalMessage = this.localize('activities.shareTerminal')
                        .replace('$(user)', `<b>${user}</b>`)
                        .replace('$(terminalId)', `<i>${terminal.id.toString()}</i>`);
                    break;
                case 'stop':
                    terminalMessage = this.localize('activities.stopSharingTerminal')
                        .replace('$(user)', `<b>${user}</b>`)
                        .replace('$(terminalId)', `<i>${terminal.id.toString()}</i>`);
                    terminalMessage = `<b>${user}</b> ${actionMessage}: ${terminal.id}`;
                    break;
                case 'changeAccess': {
                    const localizeKey = terminal.options.readOnlyForGuests
                        ? 'activities.terminalAccessChangeToReadOnly'
                        : 'activities.terminalAccessChangeToReadWrite';
                    terminalMessage = this.localize(localizeKey)
                        .replace('$(user)', `<b>${user}</b>`)
                        .replace('$(terminalId)', `<i>${terminal.id.toString()}</i>`);
                    break;
                }
                case 'executeCommand':
                    terminalMessage = this.localizedStrings['activities.executeTerminalCommand']
                        .replace('$(user)', `<b>${user}</b>`)
                        .replace('$(terminalId)', `<i>${terminal.id.toString()}</i>`);
                    break;
                default:
                    terminalMessage = ``;
                    break;
            }
            return terminalMessage;
        },
    },
};
</script>
