<template>
    <div class="activities-search">
        <vscode-dropdown
            class="search-dropdown"
            @change="onUserFilterUpdated"
            v-bind:value="userFilter">
            <vscode-option>{{ localize('All users') }}</vscode-option>
            <vscode-option v-for="user in usersArray" v-bind:key="user" v-bind:value="user">
                {{ user }}
            </vscode-option>
        </vscode-dropdown>
        <vscode-text-field
            class="search-input"
            type="text"
            @input="onSearchTextUpdated"
            v-bind:value="searchText"
            v-bind:placeholder="placeholder">
        </vscode-text-field>
    </div>
</template>

<script>
export default {
    name: 'activities-search',
    props: ['users', 'searchText', 'userFilter', 'localize'],
    computed: {
        usersArray() {
            return Object.values(this.users);
        },
        placeholder() {
            return this.localize('Search');
        },
    },
    methods: {
        onSearchTextUpdated(text) {
            this.$emit('searchTextUpdated', text);
        },
        onUserFilterUpdated(type) {
            this.$emit('userFilterUpdated', type);
        },
    },
};
</script>

<style scoped>
.activities-search {
    width: 100%;
    margin: 10px 0;
    display: flex;
    font-size: inherit;
    font-family: inherit;
}

.search-dropdown {
    min-width: 130px;
}

.search-input {
    width: 100%;
}
</style>
